import React, { useState } from "react"
import { Link } from "gatsby"
import "./CategoryTree.css"
import ComposeLogo from "./ComposeLogo";

// TypeScript Ïù∏ÌÑ∞ÌéòÏù¥Ïä§ Ï†ïÏùò
interface Post {
  fields: {
    slug: string
  }
  frontmatter: {
    title: string
    categories?: string[]
  }
}

interface Category {
  name: string
  children: Record<string, Category>
  posts: Post[]
}

interface CategoryNodeProps {
  category: Category
  posts: Post[]
  level?: number
}

interface CategoryTreeProps {
  data: {
    allMarkdownRemark: {
      nodes: Post[]
    }
  }
}

const renderSidebarTitle = (title: string) => {
  if (!title.includes("::compose::")) {
    return title;
  }

  const parts = title.split("::compose::");
  return (
    <>
      {parts.map((part, i) => (
        <React.Fragment key={i}>
          {i > 0 && <ComposeLogo />}
          {part}
        </React.Fragment>
      ))}
    </>
  );
};


const CategoryNode = ({ category, posts, level = 0 }: CategoryNodeProps) => {
  const [isOpen, setIsOpen] = useState(false)
  const hasChildren = Object.keys(category.children).length > 0
  const hasPosts = posts.length > 0
  const isCategory = hasChildren || hasPosts
  const maxDepth = 3 // ÏµúÎåÄ ÎéÅÏä§ Ï†úÌïú

  // ÏµúÎåÄ ÎéÅÏä§Ïóê ÎèÑÎã¨ÌïòÎ©¥ ÏûêÏãù Ïπ¥ÌÖåÍ≥†Î¶¨Î•º ÌòÑÏû¨ Î†àÎ≤®Ïóê Î≥ëÌï©
  if (level >= maxDepth && hasChildren) {
    return (
      <>
        <div className="category-header">
          <span className="category-name">{category.name}</span>
          {hasPosts && (
            <span className="post-count">({posts.length})</span>
          )}
        </div>
        <div className="category-content">
          {Object.values(category.children).map((child: Category) => (
            <CategoryNode
              key={child.name}
              category={child}
              posts={child.posts}
              level={level}
            />
          ))}
          {hasPosts && (
            <div className="posts">
              {posts.map((post: Post) => (
                <Link
                  key={post.fields.slug}
                  to={post.fields.slug}
                  className="post-link"
                >
                  {renderSidebarTitle(post.frontmatter.title)}
                </Link>
              ))}
            </div>
          )}
        </div>
      </>
    )
  }

  return (
    <div className="category-node">
      <button 
        className="category-header"
        onClick={() => setIsOpen(!isOpen)}
        onKeyDown={(e: React.KeyboardEvent) => {
          if (e.key === 'Enter' || e.key === ' ') {
            e.preventDefault()
            setIsOpen(!isOpen)
          }
        }}
      >
        {isCategory && (
          <span className={`arrow ${isOpen ? 'open' : ''}`}>‚ñ∂</span>
        )}
        <span className="category-name">{category.name}</span>
        {hasPosts && (
          <span className="post-count">({posts.length})</span>
        )}
      </button>
      {isOpen && (
        <div className="category-content">
          {hasChildren && (
            <div className="subcategories">
              {Object.values(category.children).map((child: Category) => (
                <CategoryNode
                  key={child.name}
                  category={child}
                  posts={child.posts}
                  level={level + 1}
                />
              ))}
            </div>
          )}
          {hasPosts && (
            <div className="posts">
              {posts.map((post: Post) => (
                <Link
                  key={post.fields.slug}
                  to={post.fields.slug}
                  className="post-link"
                >
                  {renderSidebarTitle(post.frontmatter.title)}
                </Link>
              ))}
            </div>
          )}
        </div>
      )}
    </div>
  )
}


const CategoryTree = ({ data }: CategoryTreeProps) => {
  const posts = data.allMarkdownRemark.nodes
  const categoryTree: Record<string, Category> = {}

  // Ïπ¥ÌÖåÍ≥†Î¶¨ Ìä∏Î¶¨ Íµ¨Ï°∞ ÏÉùÏÑ±
  posts.forEach((post: Post) => {
    if (post.frontmatter.categories) {
      let currentLevel = categoryTree
      post.frontmatter.categories.forEach((category: string, index: number) => {
        if (!currentLevel[category]) {
          currentLevel[category] = {
            name: category,
            children: {},
            posts: []
          }
        }
        if (index === post.frontmatter.categories.length - 1) {
          currentLevel[category].posts.push(post)
        }
        currentLevel = currentLevel[category].children
      })
    }
  })

  return (
    <div className="category-tree">
      <h4>Categories</h4>
      {Object.values(categoryTree).map((category) => (
        <CategoryNode
          key={category.name}
          category={category}
          posts={category.posts}
        />
      ))}
    </div>
  )
}


export default CategoryTree 